body {
  background-color: hsl(218, 23%, 16%);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 600px;
  font-family: "Manrope", sans-serif;
}
body > div {
  background-color: #313a49;
  width: 450px;
  text-align: center;
  padding: 30px 43px;
  border-radius: 10px;
  position: relative;
}

.adviceId {
  color: hsl(150, 100%, 66%);
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 2px;
  margin: 20px 0;
}

.advice {
  color: hsl(193, 38%, 86%);
  font-weight: 600;
  line-height: 35px;
  font-size: 24px;
}

img {
  width: 100%;
  margin: 30px 0 50px 0;
}

.mobilediv {
  display: none;
}

.dice-btn {
  background-color: hsl(150, 100%, 66%);
  width: 60px;
  height: 60px;
  position: absolute;
  border-radius: 50%;
  bottom: -30px;
  left: 45%;
  transition: 0.5s ease-in-out;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.dice-btn img {
  width: 45%;
  margin: 29%;
}

.dice-btn:hover {
  box-shadow: 1px 1px 20px hsl(150, 100%, 66%);
}

@media screen and (max-width: 600px) {
  body {
    height: 100%;
    padding-top: 20%;
  }
  body > div {
    width: 450px;
    text-align: center;
    padding: 30px;
    margin: 0 15px;
    border-radius: 10px;
    position: relative;
  }

  .advice {
    font-size: 23px;
  }
  .desktopdiv {
    display: none;
  }
  .mobilediv {
    display: block;
  }
  .dice-btn {
    left: 42%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  .dice-btn img {
    width: 45%;
    margin: 0;
  }
}
